// Sleep
// Creates an instance of a function which

export default () => {
	let timeout;
	let state;
	return (period, callback = () => {}) => {

		if (typeof period === 'number') {
			if (timeout) {
				clearTimeout(timeout);
			}
			// Switch on to sleep mode
			state = true;

			// Set the period to change the state.
			timeout = setTimeout(() => {
				state = false;
				callback();
			}, period);
		}

		return state;
	}
}
